import React from 'react';
import {Card} from '../components/card';
import {CardTitle} from '../components/cardtitle';
import '../App.css';
import {Col,Row} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMediumM } from '@fortawesome/free-brands-svg-icons';

export function PostList(props) {
    const posts = props.posts;
    const listItems = posts.map((post,index) =>
      <Col sm="12" md={{size:4}}>
        <Card>
            <CardTitle>{post.title}</CardTitle>
            <Col sm="12" md="12"><h6 className="caption">- {post["dc:creator"]}</h6></Col>
            <Col sm="12" md="12"><p className="caption">- {post["pubDate"]}</p></Col>
            <Col className = "full-button-medium"><a href={post.link} target="blank"><FontAwesomeIcon icon={faMediumM} color="#fff"/> View article on Medium</a></Col>
        </Card>
      </Col>
    );
    
    return (
      <Row>{listItems}</Row>
    );
  }