import React from 'react';
import {Card} from '../components/card';
import {CardBody} from '../components/cardbody';
import {CardTitle} from '../components/cardtitle';
import {CardImg} from '../components/cardimg';
import {Col,Badge,Row} from 'reactstrap';
import '../App.css';
import {faGithub} from '@fortawesome/free-brands-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export function WorkList(props) {
    const projects = props.projects;
    const listItems = projects.map((project,index) =>
      <Col sm="12" md="4">
        <Card>
            <CardImg source={project.imageUrl}/>
            <CardTitle>{project.title}</CardTitle>
            <CardBody>
             <p dangerouslySetInnerHTML={{__html: project.description}}></p><br/>
              <b>Relevant tags : </b>
              {project.tags.map(function(item){
             return( <h5 style={{display : 'inline'}}><Badge color="info" style={{margin:'1px'}}>{item}</Badge></h5>)  
            }
            )}
            </CardBody>
            <Col md="12" sm="12" className="full-button">
              <a href={project.github}><FontAwesomeIcon icon={faGithub}/> &nbsp;&nbsp; View on Github</a> 
            </Col>
        </Card> 
      </Col>
    );
    return (
      <Row>{listItems}</Row>
    );
  }