import React from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import {Container,Row,Col,Navbar,NavbarToggler,Collapse,Nav,NavItem} from 'reactstrap';
import "./App.css";
import {Blog} from "./views/blog";
import {Contactme} from "./views/contactme";
import {Home} from "./views/home";
import {MyProjects} from "./views/myprojects";
import {Skillset} from "./views/skillset";
import {CustomButton} from "./components/customButton";
class App extends React.Component {
  constructor(props){
    super(props);
    this.state={
      isOpen : false
    }
    this.toggle=this.toggle.bind(this);
  }

  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

render(){
  return (
  <Router> 
    <Container fluid={true} className="bg-darker">
    <Navbar color="faded" light expand="md">
          <NavbarToggler type="button" onClick={this.toggle}/>
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <Link to="/" style={{textDecoration:"none"}}>
                  <CustomButton className="outlineButton">HOME</CustomButton>
                </Link>
              </NavItem>
              <NavItem>
                <a target="blank" href="https://www.dropbox.com/s/gvvuwk0yo3im10n/Resume%28Prashant%20Singh%29%20%281%29.pdf?dl=0" style={{textDecoration:"none"}}>
                  <CustomButton className="outlineButton"><b>RESUME</b></CustomButton>
                </a>
              </NavItem>
              <NavItem>
                <Link to="/projects/" style={{textDecoration:"none"}} >
                  <CustomButton className="outlineButton">MY PROJECTS</CustomButton>
                </Link>
              </NavItem>
              <NavItem>
                <Link to="/skillset/" style={{textDecoration:"none"}}>
                  <CustomButton className="outlineButton">SKILLS</CustomButton>
                </Link>
              </NavItem>
              <NavItem>
                <Link to="/contact/" style={{textDecoration:"none"}}>
                  <CustomButton className="outlineButton">CONTACT ME</CustomButton>
                </Link>  
              </NavItem>
              <NavItem>
                <Link to="/blog/" style={{textDecoration:"none"}}>
                  <CustomButton className="outlineButton">BLOG</CustomButton>
                </Link>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
    </Container>
    <Container className="padding">
      <Row>
        <Col md="12">
          <Row>
            <Route exact path="/" component={Home}/>
            <Route path="/projects/" component={MyProjects}/>
            <Route path="/skillset/" component={Skillset}/>
            <Route path="/contact/" component={Contactme}/>
            <Route path="/blog/" component={Blog}/>
          </Row>
          
        </Col>
      </Row>
    </Container>
  </Router>
  );
  }
}

export default App;
