import React, { Component } from 'react';   //import {fetchPosts} from '../util/getPostsAPI';
import axios from 'axios';
import {Col,Jumbotron} from 'reactstrap';
import ".././css/post.css";
import "../App.css";
import {PostList} from "../components/postlist";
export class Blog extends Component {
    
    constructor(props){
        super(props);
        this.state = {
            posts : [],
            dataLoaded : false,
            error : null
        }
        this.createMarkup = this.createMarkup.bind(this);
   }

    createMarkup = (post) => {

            return {__html: post};
    
    }

    componentDidMount(){
        console.log("COMPONENT DID MOUNT");
        const thisRef = this;
        axios.get('https://powerful-wave-93367.herokuapp.com/posts').then(function(response){    
            console.log(response.data);    
        thisRef.setState((previousState)=>({
                posts : response.data,
                error : null,
                dataLoaded : true
            }));
            console.log(thisRef.state);
        }).catch(function(error){
            thisRef.setState((previousState)=>({
                posts : previousState.posts,
                dataLoaded : true,
                error : error
            }));
        });
    }
    
    render() {
        if(this.state.dataLoaded){

            return (
            <Col sm="12" md={{size : 12, offset : 0}}>
                    {/*<Row className="padding">
                        <Col sm="12" md="12">
                            <p className="post-heading">{this.state.posts["0"]["title"]}</p>
                        </Col>
                    </Row>
                    <Row className="padding">
                        <Col sm="12" md="12" className="post-wrapper" dangerouslySetInnerHTML={this.createMarkup(this.state.posts[0]["content:encoded"])}></Col>
                    </Row>*/}
                <Jumbotron style={{backgroundColor : '#fff'}}>
                 <h1 className="display-3">Blog</h1>
                 <br/>
                 <br/>
                 <p className="lead">A list of my latest posts on Medium.
                 </p>
                </Jumbotron>
                <Col sm="12" md={{size:12}}>
                <PostList posts={this.state.posts}></PostList>
                </Col>
            </Col>
            )
        }
        else{
            return(
                <Col sm="12" md={{size : 12}}>
                    {/*<Row className="padding">
                    <Col sm="12" md="12">
                        <p className="post-heading">{this.state.posts["0"]["title"]}</p>
                    </Col>
                    </Row>
                    <Row className="padding">
                    <Col sm="12" md="12" className="post-wrapper" dangerouslySetInnerHTML={this.createMarkup(this.state.posts[0]["content:encoded"])}></Col>
                    </Row>*/}
                    <Jumbotron style={{backgroundColor : '#fff'}}>
                    <h1 className="display-3">Blog</h1>
                    <br/>
                    <br/>
                    <p className="lead">A list of my latest posts on Medium.
                    </p>
                    </Jumbotron>
                    <Col md={{size : 1, offset : 5}} sm={{size : 1, offset : 5}}>
                        <img src={require('../assets/ajax-loader.gif')} height="10px" weight="20px" className="App-logo"  alt="Loading"/>
                    </Col>
                </Col>
            )
        }
    }
}
